<template>
  <div>
    <Main></Main>
  </div>
</template>

<script>
import "@/assets/css/base.css";
import "@/assets/css/common.css";
import "@/assets/css/index.css";
// 正确导入jQuery并暴露为全局变量
import $ from 'jquery';
window.$ = $; // 将jQuery暴露为全局变量

import Main from "@/components/Main.vue";
export default {
  components: {
    Main,
  },
  mounted(){
     // 动态导入side.js，确保此时jQuery已经加载并暴露为全局变量
    import("@/assets/js/side.js").then(() => {
      console.log('Side.js已加载');
    }).catch(error => {
      console.error('Side.js加载失败:', error);
    });

      // 在组件挂载后导入并初始化轮播图
    import('@/assets/js/slideshow.js').then(() => {
      // 轮播图已经在 slideshow.js 中自动初始化
      console.log('轮播图已初始化');
    }).catch(error => {
      console.error('轮播图初始化失败:', error);
    });
  }
};
</script>

